<template>
  <div id="container"></div>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader'
import bus from '@/bus/bus'
window._AMapSecurityConfig = {
  securityJsCode: '311870070113192a5f5776dd2a8b0486'
}
export default {
  data() {
    return {
      map: null,
      autoOptions: {
        input: ''
      },
      searchPlaceInput: '',
      auto: null,
      placeSearch: null,
      district: null,
      polygons: [],
      showHeatOrNot: false,
      heatmap: null,
      heatmapList: null,
      grade: "错误",
      polygon_grade_one: [],
      markers:[]
    }
  },
  methods: {
    initMap() {
      AMapLoader.load({
        key: 'b1ffdc158b6df44fabca3c066d4f4a43', // 申请好的Web端开发者Key，首次调用 load 时必填
        version: '2.0', // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
        plugins: ['AMap.ToolBar', 'AMap.Scale', 'AMap.HawkEye', 'AMap.MapType', 'AMap.Geolocation', 'AMap.AutoComplete', 'AMap.PlaceSearch', 'AMap.Geocoder'] // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      })
        .then(AMap => {
          this.map = new AMap.Map('container', {
            //设置地图容器id
            viewMode: '3D', //是否为3D地图模式
            zoom: 10, //初始化地图级别
            center: [101.704657, 26.547333] //初始化地图中心点位置
          })
          this.map.addControl(new AMap.Scale())
          this.map.addControl(new AMap.ToolBar())
          this.map.addControl(new AMap.MapType())
          // this.map.addControl(new AMap.Geolocation())
          console.log(this.autoOption)
          this.auto = new AMap.AutoComplete({
            input: 'searchInput',
            city: "510400",

          })
          this.placeSearch = new AMap.PlaceSearch({
            map: this.map,
            pageSize: 1, // 单页显示结果条数
            pageIndex: 1, // 页码
            city: "510400", // 兴趣点城市
            citylimit: true,  //是否强制限制在设置的城市内搜索
            // panel: "panel", // 结果列表将在此容器中进行展示。
            autoFitView: true // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
          }) //构造地点查询类
          this.auto.on('select', this.select)
          this.map.on('click', e => {
            let lat = e.lnglat.lat
            let lng = e.lnglat.lng
            this.getLngLatService(lat, lng)
          })



          //多边形示例
          const path = [
            new AMap.LngLat(101.719165, 26.570229),
            new AMap.LngLat(101.726925, 26.54451),
            new AMap.LngLat(101.763708, 26.555951),
          ];
          const polygon_grade1 = new AMap.Polygon({
            path: path,
            fillColor: 'blue', // 多边形填充颜色
            borderWeight: 2, // 线条宽度，默认为 1
            strokeColor: 'blue', // 线条颜色,
            zIndex: 2,
            bubble: true
          });
          //多边形 Polygon对象添加到 Map
          this.map.add(polygon_grade1);
          // 缩放地图到合适的视野级别
          this.map.setFitView([polygon_grade1])
          this.polygon_grade_one.push(polygon_grade1)

        })
        .catch(e => {
          console.log(e)
        })
    },

    select(e) {
      let polygon_grade=this.polygon_grade_one
      this.placeSearch.setCity(e.poi.adcode)
      this.placeSearch.search(e.poi.name, function (status, result) {
        console.log(status, result)
        let res = {
          pos: [result.poiList.pois[0].location.lng, result.poiList.pois[0].location.lat],
          address: result.poiList.pois[0].cityname+result.poiList.pois[0].adname+result.poiList.pois[0].name,
          grade: "错误"
        }
    
        let lnglat = new AMap.LngLat(result.poiList.pois[0].location.lng, result.poiList.pois[0].location.lat)
        if (polygon_grade[0].contains(lnglat)) {
          res.grade = "1"
        }
        console.log(res)
        //需求：固定的窗体信息进行展示！
        bus.$emit('shareAddressDetails', res)
      }) //关键字查询查询
      this.map.setZoom(16, true, 1)
    },

    feedBack(msg, feedBackType) {
      this.$message({
        showClose: true,
        message: msg,
        type: feedBackType
      })
    },


    //逆向地理编码服务
    getLngLatService(lat, lng) {

      let pos = [lng, lat]
      let lnglat = new AMap.LngLat(lng, lat)
      let geocoder = new AMap.Geocoder({
        // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
        city: '510400'
      })

      //1.点击地图任意位置生成一个marker
      let marker = new AMap.Marker({
        position: new AMap.LngLat(lng, lat),
      })
      // this.map.remove()
      this.map.add(marker)
      if (this.markers.length>0) {
        this.map.remove(this.markers)
      }
      this.markers.push(marker)
      let address = ''

      let grade = this.grade
      let polygon_grade = this.polygon_grade_one
      // console.log(polygon_grade_one[0])
      if (polygon_grade[0].contains(lnglat)) {
        grade = "1"
      }
      //2.将位置转化为坐标点-->地理信息
      //3.根据地理信息（地址）进行搜索获取详细信息！
      geocoder.getAddress(lnglat, (status, result) => {
        if (status === 'complete' && result.regeocode) {
          address = result.regeocode.formattedAddress
          let res = {
            pos: pos,
            address: address,
            grade: grade
          }
          //需求：固定的窗体信息进行展示！
          bus.$emit('shareAddressDetails', res)
        } else {
          log.error('根据经纬度查询地址失败')
        }
      })
    }
  },
  mounted() {
    //DOM初始化完成进行地图初始化
    this.initMap()
  },
  created() {
    bus.$on('shareUserInput', val => {
      this.autoOptions.input = val.inputId
      this.searchPlaceInput = val.userInput
    })

  },
  watch: {
    searchPlaceInput(newValue) {

      let geocoder = new AMap.Geocoder({
        city: '510400' // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
      })

      geocoder.getLocation(newValue, function (status, result) {
        if (status === 'complete' && result.info === 'OK') {
          // result中对应详细地理坐标信息
          let lnglat = result.geocodes[0].location

          marker.setPosition(lnglat);
          map.add(marker);
          map.setFitView(marker);
        }
      })


    },
    showHeatOrNot(newValue) {
      if (newValue) {
        this.showHeatMap()
      } else {
        this.heatmap.hide()
      }
    }
  }
}
</script>

<style lang="less" scoped>
#container {
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%;
}
</style>
